import React, { useCallback, useState} from "react";
import ReactFlow, {
  // addEdge,
  // MiniMap,
  Controls,
  // Background,
  useNodesState,
  useEdgesState
} from "reactflow";
import "reactflow/dist/style.css";
import {
  nodes as initialNodes,
  edges as initialEdges,
  resourceNodeStyle
} from "./nodes";
import { calculateLevelsAndCounts } from "./nodeCount";
import { useEffect } from 'react';

const onInit = (reactFlowInstance) =>
  console.log("flow loaded:", reactFlowInstance);

// const onConnectStart = () => false;


function calculateLevelWidths(levelCount, nodeWidth, spaceWidth) {
  const levelWidths = {};

  for (const level in levelCount) {
    if (levelCount.hasOwnProperty(level)) {
      const nodeCount = levelCount[level];
      const levelWidth = (nodeCount * nodeWidth) + ((nodeCount - 1) * spaceWidth);
      console.log("level: " + level)
      console.log("node count: " + nodeCount)
      console.log("level width: " + levelWidth)
      levelWidths[level] = -(levelWidth/2);
    }
  }

  return levelWidths;
}

function adjustNodePositions(nodes, nodeLevels, levelCount, nodeWidth, spaceWidth) {
  // Calculate the total height for each level
  const levelHeight = 300; // You can adjust this value as needed
  const levelWidths = calculateLevelWidths(levelCount, nodeWidth, spaceWidth)

  // Iterate through the nodes and adjust their positions
  console.log(nodeLevels)
  console.log(levelCount)
  nodes.forEach(node => {
    console.log(node.id)
    const level = nodeLevels[node.id];
    const count = levelCount[level];
    const width = levelWidths[level];

    if (level !== undefined && count !== undefined) {
      // Calculate the new x-position based on the level and node count
      const newX = width;
      levelWidths[level] += (nodeWidth + spaceWidth) // update level start position for next node
      
      // Update the node's position
      node.position.x = newX;
      node.position.y = level * levelHeight;
    }
  });

  return nodes;
}


const Flowchart = () => {
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
  const [root, setRoot] = useState('1');
  const [lastRoot, setLastRoot] = useState(null);
  // const { nodeLevels, levelCount } = calculateLevelsAndCounts(edges);


  const handleDoubleClick = (event, node) => {
    if ( node.id == "GB" ) {
      // console.log("going back to node with ID: " + lastRoot)
      setRoot('1')
    } else if (node.style !== resourceNodeStyle){
      setRoot(node.id)
    } else {
      const link = document.createElement("a");
      const website = node.websiteLink
      if (website !== null){
        window.open(website, '_blank');
      }
      
      if (node.fileName !== null){
        link.href = node.fileName
        link.download = node.fileName
        
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }
    }
  };

  const findImmediateChildren = (nodes, edges, nodeId, alwaysIncludeIds) => {
    // Initialize the result arrays
    const resultNodes = [];
    const resultEdges = [];
  
    // Find and add the specified node to the resultNodes array
    const specifiedNode = nodes.find(node => node.id === nodeId);
    if (specifiedNode) {
      resultNodes.push(specifiedNode);
    }
  
    // Iterate over the edges to find and add the immediate children
    edges.forEach(edge => {
      if (edge.source === nodeId) {
        resultEdges.push(edge);
        const childNode = nodes.find(node => node.id === edge.target);
        if (childNode) {
          resultNodes.push(childNode);
        }
      }
    });

   // Ensure that certain nodes are always included
    alwaysIncludeIds.forEach(id => {
      const nodeAlreadyIncluded = resultNodes.some(node => node.id === id);
      if (!nodeAlreadyIncluded) {
        const nodeToInclude = nodes.find(node => node.id === id);
        if (nodeToInclude) {
          resultNodes.push(nodeToInclude);
        }
      }
    });   
  
    // Return the result
    return { resultNodes, resultEdges };
  }

  useEffect (() => {
    const { resultNodes, resultEdges } = findImmediateChildren(initialNodes, initialEdges, root, ["TUT", "GB"]);
    setEdges(resultEdges)
    const { nodeLevels, levelCount } = calculateLevelsAndCounts(resultEdges, root);
    console.log("node levels: " + nodeLevels)
    console.log("level counts: " + levelCount)
    setNodes(adjustNodePositions(resultNodes, nodeLevels, levelCount, 180, 50)) 
  }, [initialNodes, initialEdges, root])

  return (
    <ReactFlow
      nodes={nodes}
      edges={edges}
      onNodeDoubleClick={handleDoubleClick}
      onInit={onInit}
      fitView
      attributionPosition="top-right"
    >
      <Controls />
    </ReactFlow>
  );
};

export default Flowchart;
