export function calculateLevelsAndCounts(edges, root) {
    const nodeLevels = {}; // Dictionary to store node levels
    const levelCount = {}; // Dictionary to store node count on each level
  
    const adjacencyList = {}; // Create an adjacency list to represent the tree structure
  
    // Build the adjacency list
    for (const edge of edges) {
      if (!adjacencyList[edge.source]) {
        adjacencyList[edge.source] = [];
      }
      adjacencyList[edge.source].push(edge.target);
    }
  
    const queue = [{ node: root, level: 0 }];
  
    while (queue.length > 0) {
      const { node, level } = queue.shift();
      nodeLevels[node] = level;
  
      if (!levelCount[level]) {
        levelCount[level] = 0;
      }
      levelCount[level]++;
  
      if (adjacencyList[node]) {
        for (const child of adjacencyList[node]) {
          queue.push({ node: child, level: level + 1 });
        }
      }
    }
  
    return { nodeLevels, levelCount };
  }